.gallery-list{
    display: grid; 
  grid-auto-columns: 1fr; 
  grid-template-columns: repeat(7, 1fr); 
  grid-template-rows: auto; 
  gap: 0px 0px; 
  align-content: center; 
  height: 430px; 
}
